/* Первый экран состоит из двух блоков
	Шапки, где только меню и второй блок с Папановым и заголовком
	Второй блок поднят вверх отрицательным маржином
	У гамбургера есть маржин 30пкс сверху, поэтому блок
	с меню в адаптивке позиционирован на -30пкс вверх.

	Появление меню происходит по добавлению двух классов тегу menu:
	- первый класс отображает элемент (дисплей блок);
	- второй добавляется с задержкой в 1мс для анимации ширины;
 */

header.header {
  height: 120px;
}

header.header .wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

header.header .wrapper .menu-icons {
  margin-top: 30px;
  display: none;
  cursor: pointer;
  position: relative;
  width: 30px;
  height: 30px;
}

header.header .wrapper .menu-icons .visible-toggle {
  opacity: 0 !important;
  z-index: 2;
}

header.header .wrapper .menu-icons #hamburger-ico {
  opacity: 1;
  z-index: 3;
}

header.header .wrapper .menu-icons #hamburger-ico:hover {
  fill: #00dc96;
}

header.header .wrapper .menu-icons #hamburger-ico.visible-toggle ~ #close-ico {
  opacity: 2;
  z-index: 3;
}

header.header .wrapper .menu-icons #close-ico {
  opacity: 0;
  z-index: 2;
}

header.header .wrapper .menu-icons #close-ico:hover {
  fill: #00dc96;
}

header.header .wrapper .menu-icons #hamburger-ico,
header.header .wrapper .menu-icons #close-ico {
  display: block;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 0;
  left: 0;
  transition: opacity .5s;
}

header.header .wrapper menu {
  padding-start: 0px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
}

header.header .wrapper menu a.link-tel {
  color: #000;
}

header.header .wrapper menu nav {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

header.header .wrapper menu nav li + li {
  margin-left: 10px;
}

header.header .wrapper menu nav li {
  z-index: 3;
}

header.header .wrapper menu nav li a {
  color: #000;
  border-bottom: 2px solid transparent;
  transition: border-bottom 0.5s;
}

header.header .wrapper menu nav li a:hover {
  border-bottom-color: #00dc96;
}

