header.header.scroll-header {
  position: fixed;
  padding: 10px 0;
  animation: head-appear 0.5s;
  width: 100%;
  top: 0;
  left: 0;
  background-color: #fff;
  z-index: 1000;
  height: auto;
  box-shadow: -1px 1px 1px 0px rgba(0, 0, 0, 0.3);
}

