    opacity: 2;
    z-index: 3;
  }

  header.header .wrapper #close-ico {
    opacity: 0;
    z-index: 2;
  }

  header.header .wrapper #close-ico:hover {
    fill: #00dc96;
  }

  header.header .wrapper #hamburger-ico,
  header.header .wrapper #close-ico {
    display: block;
    width: 30px;
    height: 30px;
    position: absolute;
    top: 0;
    left: 0;
    transition: opacity .5s;
  }

  header.header .wrapper menu {
    display: none;
    overflow: hidden;
    position: absolute;
    width: 0%;
    padding: 60px 0;
    height: auto;
    top: -30px;
    right: 0;
    transition: width .5s;
    background-color: #fff;
    z-index: 1;
